.accueil-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.Accueil {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.Accueil img {
    max-width: 95%;
}

.Accueil h1 {
    padding: 0 2rem;
}

.Accueil .arrow-container {
    width: 100vw;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.Accueil .arrow {
    width: 80px;
    height: 80px;
    transform: rotate(45deg);
    border-right: 1px solid var(--color-1);
    border-bottom: 1px solid var(--color-1);
    transition: 500ms;
}

.Accueil .arrow:hover {
    width: 100px;
    height: 100px;
}