.Navigation {
    width: 100%;
    max-width: 1590px;
    height: var(--nav-height);
    position: fixed;
}

.Navigation ul {
    display: flex;
    width: 100%;
    justify-content: flex-end;
}

.Navigation li {
    margin-right: 1rem;
    transition: 500ms;
    font-size: 1.5rem;
}

/* .Navigation li:hover {
    font-size: 2rem;
} */
.Navigation li:hover {
    color: var(--color-1);
}

.Navigation.active {
    font-size: 2rem;
}

.Navigation a {
    text-decoration: none;
    color: inherit;
    font-weight: 200;
}

@media only screen and (max-width: 281px) {
    /* html {
      font-size: 10px;
    }
  
    .Accueil .arrow-container {
      transform: scale(0.7);
      margin-bottom: -60px;
    } */

    :root {
        --nav-height: 50px;
    }
}