@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,200;1,300;1,400;1,500&display=swap');

:root {
  --background-color: #08090b;
  --text-color: #f2faff;
  --color-1: #d3fbd8;
  --nav-height: 80px;
}

html {
  font-size: 16px;
}

@media (prefers-reduced-motion: no-preference) {
  * {
    scroll-behavior: smooth;
  }
}


* {
  box-sizing: border-box;
}

.progress-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: var(--color-1);
  transform-origin: 0%;
  z-index: 0;
}

body {
  font-family: 'Poppins', serif;
  background: var(--background-color);
  color: var(--text-color);
  width: 100%;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

h1 {
  font-size: 3rem;
  font-weight: 200;
}

h2,
h3,
h4 {
  font-weight: 400;
}

h3,
h2 {
  margin-top: 4rem;
}

p {
  font-weight: 200;
}

table {
  border-spacing: 1rem;
  margin-top: 2rem;
}

th {
  font-weight: 400;
  border-right: 1px solid var(--text-color);
}

td {
  font-weight: 300;
  padding-left: 1rem;
}

ul {
  list-style: none;
  padding: 0;
}

section:not(:nth-child(1)) {
  border-top: 1px solid var(--color-1);
}

section:not(:nth-child(1)),
.section-header {
  width: 100%;
  max-width: 800px;
}

.section-header {
  color: var(--color-1);
}

.artists-container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  /* justify-content: center; */
}

.empty-artist-card {
  width: 100%;
  max-width: 400px;
  height: 400px;
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

.info-container li {
  font-weight: 200;
  padding-left: 1rem;
}

@media only screen and (max-width: 900px) {
  .App {
    padding: 0 1rem;
  }
}

@media only screen and (max-width: 831px) {
  .artists-container {
    justify-content: center;
  }
}

@media only screen and (max-width: 767px) {

  .progress-bar {
    height: 2px;
    top: var(--nav-height);
  }

  .Navigation {
    background: var(--background-color);
  }
}

@media only screen and (max-width: 400px) {
  html {
    font-size: 14px;
  }
}

@media only screen and (max-width: 281px) {
  html {
    font-size: 10px;
  }

  .Accueil .arrow-container {
    transform: scale(0.7);
    margin-bottom: -60px;
  }

  .Navigation {
    height: 30px;
  }
}