.ArtistCard {
    width: 100%;
    max-width: 400px;
    height: 400px;
    color: var(--text-color);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
}

.ArtistCard h1 {
    align-self: flex-start;
    font-size: 2.5rem;
    margin: 0;
    font-weight: 200;
}

.ArtistCard p {
    border: 1px solid var(--color-1);
    width: 200px;
    height: 50px;
    display: flex;
    align-items: flex-end;
    padding: 0 0 0.25rem 0.5rem;
    transition: 500ms;
    font-weight: 300;
}

.ArtistCard p:hover {
    background: var(--background-color);
    padding-left: 1rem;
}

.ArtistCard a {
    color: inherit;
    text-decoration: none;
}

@media only screen and (max-width: 831px) {
    .ArtistCard {
        max-width: unset;
    }
}